.acf-menu-item-fields {
	float: none !important;
}

.swatch {
	border: 2px solid #fff;
	border-radius: 8px;
	display: inline-flex;
	flex-direction: column;
	height: rem(224);
	justify-content: flex-end;
	margin-bottom: $gap-md;
	padding: rem(16);
	position: relative;
	vertical-align: middle;
	width: rem(176);

	&:before {
		font-size: rem(18);
	}

	&:after {
		font-size: rem(14);
		line-height: ( 20 / 14 );
		margin-top: rem(6);
		opacity: .6;
		text-transform: uppercase;
	}

	&--sm {
		border: 1px solid rgba(black, .15);
		height: rem(48);
		padding: 0;
		width: rem(48);

		&:before ,
		&:after {
			display: none;
		}
	}

	&.bg-none {
		background-image: linear-gradient(45deg, #ddd 25%, transparent 25%), linear-gradient(-45deg, #ddd 25%, transparent 25%), linear-gradient(45deg, transparent 75%, #ddd 75%), linear-gradient(-45deg, transparent 75%, #ddd 75%);
		background-size: 20px;
		background-position: 0 0, 0 10px, 10px -10px, -10px 0;
	}

	@each $modifier, $background-color, $color in $swatches {
		&.bg-#{$modifier} {

			&:before {
				content: "$#{$modifier}";
			}

			&:after {
				content: "#{$background-color}";
			}
		}
	}
}

.fancy-toggles {

	ul {
		display: flex;
		flex-wrap: wrap;
		margin: 0 rem(-4);
	}

	li {
		display: block;
		margin: rem(4) !important;
	}

	input[type=checkbox],
	input[type=radio] {
		display: none;

		&:checked {

			+ * {
				box-shadow: 0 0 rem(10) rem(2) dodgerblue;
			}
		}
	}
}

// sass-lint:disable no-ids
#screen-options-wrap {
	visibility: visible;
}
// sass-lint:enable no-ids

.hidden[style*="display: block;"] {
	visibility: visible;
}
