/**
 * COLORS
 * Contains all brand color and gray tone variables.
 *
 * @since 1.0.0
 */



/**
 * Theme Color Map
 *
 * @since 1.0.0
 */
$theme-colors: (
	"red": (
		"base": #ee0000,
	),
	"blue": (
		"base": #0016ff,
		"l": #01a2e7,
	),
	"gray": (
		"xd": #343639,
		"d": #55585d,
		"base": #9da2ab,
		"l": #dbdde1,
		"xl": #f3f3f3
	)
);

/**
 * Retrieves a theme color from $theme-colors, with optional variant argument.
 *
 * @since 1.0.0
 *
 * @param  {String}  $color  The color variable to retrieve.
 * @param  {String}  $tone   The variant of the $color argument.
 * @return {String}          The color value.
 */
@function theme-color($color, $tone: "base") {
	@return map-get(map-get($theme-colors, $color), $tone);
}

/**
 * Helper variables for common colors used throughout the theme.
 *
 * @since 1.0.0
 */

// Brand colors.
$primary-color: theme-color("blue");
$primary-color-d: darken( $primary-color, 15% );
$primary-color-l: lighten( $primary-color, 15% );
$secondary-color: theme-color("blue", "l");
$secondary-color-d: darken( $secondary-color, 15% );
$secondary-color-l: lighten( $secondary-color, 15% );
$tertiary-color: theme-color("gray", "xd");
$tertiary-color-d: darken($tertiary-color, 10%);
$tertiary-color-l: lighten($tertiary-color, 10%);

// Grays.
$gray-xd: theme-color("gray", "xd");
$gray-d: theme-color("gray", "d");
$gray: theme-color("gray");
$gray-l: theme-color("gray", "l");
$gray-xl: theme-color("gray", "xl");

// Body text color.
$base-font-color: $gray-xd;

// Link text color.
$link-color: $primary-color;
$link-color-hover: $primary-color-d;

// Muted text.
$muted: .6;

/**
 * Swatches Color Map.
 *
 * - Used for building classes in helpers/_background.scss for background classes.
 * - The swatches can also be added to ACF color pickers.
 *
 * @since 1.0.0
 */
 $swatches: (
	// theme colors
	("primary", $primary-color, #fff),
	("primary-l", $primary-color-l, #fff),
	("primary-d", $primary-color-d, #fff),
	("secondary", $secondary-color, #fff),
	("secondary-l", $secondary-color-l, #fff),
	("secondary-d", $secondary-color-d, #fff),
	("tertiary", $tertiary-color, #fff),
	("tertiary-l", $tertiary-color-l, #fff),
	("tertiary-d", $tertiary-color-d, #fff),
	// neutrals
	("gray-xd", $gray-xd, #fff),
	("gray-d", $gray-d, #fff),
	("gray", $gray, null),
	("gray-l", $gray-l, null),
	("gray-xl", $gray-xl, null),
	("white", #fff, null),
);
