@each $modifier, $color, $nouse in $swatches {
	.has-#{$modifier}-color {
		color: $color !important;

		@if($modifier == "white") {
			a {
				color: inherit;
			}
		}
	}
}

@each $modifier, $color, $text-color in $swatches {
	.has-#{$modifier}-background-color {
		background-color: $color !important;

		@if $text-color {
			color: $text-color;

			@if $modifier != "white" {
				*:not(.card) ,
				*:not(.card *) {
					color: inherit;
				}
			}
		}
	}
}

.has-background-dim {

	&::before {
		background-color: inherit;
		opacity: .5;
	}

	&-10 {

		&::before {
			opacity: .1;
		}
	}

	&-20 {

		&::before {
			opacity: .2;
		}
	}

	&-30 {

		&::before {
			opacity: .3;
		}
	}

	&-40 {

		&::before {
			opacity: .4;
		}
	}

	&-60 {

		&::before {
			opacity: .6;
		}
	}

	&-70 {

		&::before {
			opacity: .7;
		}
	}

	&-80 {

		&::before {
			opacity: .8;
		}
	}

	&-90 {

		&::before {
			opacity: .9;
		}
	}

	&-100 {

		&::before {
			opacity: 1;
		}
	}
}
