// Reusable spacers.
$gap-px: 15;
$gap-px--xs: $gap-px / 4;
$gap-px--sm: $gap-px / 2;
$gap-px--md: $gap-px;
$gap-px--lg: $gap-px * 2;
$gap-px--xl: $gap-px * 4;
$gap-xs: rem($gap-px--xs);
$gap-sm: rem($gap-px--sm);
$gap-md: rem($gap-px--md);
$gap-lg: rem($gap-px--lg);
$gap-xl: rem($gap-px--xl);

// Gutters.
$gutter: $gap-px;

// Max-width of a paragraph of content.
$max-content-width: 820;

// Site gutters.
$site-gutter-base: $gap-px--sm;
$site-gutter-xs: null;
$site-gutter-sm: $gap-px--md;
$site-gutter-md: null;
$site-gutter-lg: null;

// Get the largest available gutter size based on the variables above.
@function get-site-gutter-max() {
	@if($site-gutter-lg) {
		@return $site-gutter-lg;
	}

	@if($site-gutter-md) {
		@return $site-gutter-md;
	}

	@if($site-gutter-sm) {
		@return $site-gutter-sm;
	}

	@if($site-gutter-xs) {
		@return $site-gutter-xs;
	}

	@if($site-gutter-base) {
		@return $site-gutter-base;
	}

	@return 0;
}

// Try to get the intended gutter variable, but fall back to the largest if it's null.
@function get-site-gutter($site-gutter) {
	@if($site-gutter) {
		@return $site-gutter;
	} @else {
		@return get-site-gutter-max();
	}
}

// Content widths.
$content-width-default: 820;
$content-width-default--gutters: $content-width-default + (get-site-gutter-max() * 2);
$content-width-wide: 1140;
$content-width-wide--gutters: $content-width-wide + (get-site-gutter-max() * 2);

// Blocks Spacing
$block-spacing--base: $gap-lg;
$block-spacing--md: $gap-xl;

$sidebar-width: 280;

$wp-admin-bar-height-desktop: 32;
$wp-admin-bar-height-mobile: 46;

// Sticky footer.
$sticky-footer: true;
